@import url(https://fonts.googleapis.com/css2?family=Exo:wght@300;400;500;600;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* *{
  border: 1px solid gold;
} */

body, html {
  font-family: 'Exo', sans-serif; 
  background-color: #f9ecdc;
  width: 100%;
}



.layout-children{
  padding-top: 120px;
  /* height: 100vh; */
  padding-bottom: 100px;
  margin-left: 35px;
  margin-right: 35px;
}


.post-cards {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  justify-content: space-evenly;
  
  /* height: 100vh; */
  
}


.Header {
  display: flex;
  justify-content: space-between;
  margin-top: 0 auto;
  margin-bottom: 0px;
  width: 100%;
  height: 60px;
  position: fixed;
  top: 0;
  border-bottom: 1px solid #CFCFCF;
  box-shadow: 1px 1px 4px #cfcfcf;
  background: 	#0f344f;
  padding-left: 35px;
  padding-right: 35px;
  padding-top: 20px;
}

.header-links {
  text-decoration: none;
  color: white;
}

.links {
  font-size: 20px;
  text-align: center;
  padding-right: 200px;
  padding-top: 8px;
}

#header-title{
  font-size: 32px;
  color: 	#ecc28d;
  font-weight: bolder;
  padding-left: 150px;
}
.footer{
  padding-right: 10px;
  padding-left: 10px;
  display: flex;
  justify-content: space-between;
  background-color: #0f344f;
  color: 	#78a2ae;
}
.post-card{
  /* display: flex; */
  width: 280px;
  margin: 15px;
  text-decoration: none;
  /* justify-content: center; */
  text-align: center;
  box-shadow: 5px 5px 15px #b45c03;
  border-radius: 15px;
  padding-top: 15px;
}

.card{
  text-decoration: none;
  color: #548889;
  font-weight: 500;
}



img{
  height: 70px;
  align-content: center;
}
.post {
  display:flex;
  flex-direction:column;
  align-items: center;
  margin:20px;
  padding:15px;
}

.post-title{
  font-weight: bold;
}

.post-img {
  width: 250px;
  height: 100%;
}

.post-author{
  font-size: 20px;
  padding: 10px;
}

.post-content{
  font-size: 14px;
  padding: 10px;
}


.post-edit {
  display: flex;
  padding: 100px;
  padding-top: 80px;
  background-color: #F9ECDC;
  margin-top: -50px;
}

.image-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.edit-post-image {
  width: 500px;
  padding: 0 40px;
  padding-bottom: 5px;
}

.edit-input-image-link {
  width: 300px;
  text-align: center;
  font-size: 20px;
  font-family: "";
}

.edit-form {
  font-family: "";
  line-height: 1.6;
}

.input-name {
  font-family: "";
  font-size: 16px;
  padding: 10px;
  letter-spacing: 0.9px;
  padding: 5px;
}

.textarea-content {
  font-family: "";
  font-size: 16px;
  padding: 10px;
  letter-spacing: 0.9px;
  line-height: 1.6;
}

.save-button {
  font-size: 22px;
  font-family: "";
  height: 40px;
  width: 200px;
  margin-top: 40px;
  background-color: #0f344f;
  color: white;
  border: none;
  box-shadow: 2px 2px 10px 2px #cecece;
  cursor: pointer;
}

@media (max-width: 500px) {
  .input-name {
    margin-left: -10px;
    padding-left: -10px;
    font-size: 26px;
  }
  .edit-form {
    padding-left: 0px;
  }
  .textarea-content {
    width: 100%;
    padding-left: 0px;
  }
  .post-edit {
    width: 100%;
    flex-direction: column;
  }
  .image-container {
    width: 100%;
  }
  .edit-post-image {
    width: 300px;
  }
}

