.post-edit {
  display: flex;
  padding: 100px;
  padding-top: 80px;
  background-color: #F9ECDC;
  margin-top: -50px;
}

.image-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.edit-post-image {
  width: 500px;
  padding: 0 40px;
  padding-bottom: 5px;
}

.edit-input-image-link {
  width: 300px;
  text-align: center;
  font-size: 20px;
  font-family: "";
}

.edit-form {
  font-family: "";
  line-height: 1.6;
}

.input-name {
  font-family: "";
  font-size: 16px;
  padding: 10px;
  letter-spacing: 0.9px;
  padding: 5px;
}

.textarea-content {
  font-family: "";
  font-size: 16px;
  padding: 10px;
  letter-spacing: 0.9px;
  line-height: 1.6;
}

.save-button {
  font-size: 22px;
  font-family: "";
  height: 40px;
  width: 200px;
  margin-top: 40px;
  background-color: #0f344f;
  color: white;
  border: none;
  box-shadow: 2px 2px 10px 2px #cecece;
  cursor: pointer;
}

@media (max-width: 500px) {
  .input-name {
    margin-left: -10px;
    padding-left: -10px;
    font-size: 26px;
  }
  .edit-form {
    padding-left: 0px;
  }
  .textarea-content {
    width: 100%;
    padding-left: 0px;
  }
  .post-edit {
    width: 100%;
    flex-direction: column;
  }
  .image-container {
    width: 100%;
  }
  .edit-post-image {
    width: 300px;
  }
}
